import type { EventEmitter } from '@stencil/core';
import { Component, Element, Event, h, Method, Prop, Watch, Host } from '@stencil/core';
import type { IconName } from '@fortawesome/fontawesome-svg-core';

@Component({
	tag: 'o-alert',
	styleUrl: 'o-alert.scss',
	shadow: true
})
export class OAlert {
	@Element() host!: HTMLOAlertElement;

	/** Indicates whether or not the alert is open. */
	@Prop({ mutable: true, reflect: true }) open = false;

	/** The variant of the alert. */
	@Prop({ reflect: true }) variant: 'primary' | 'success' | 'neutral' | 'warning' | 'danger' = 'primary';

	/** Emitted when the alert opens. */
	@Event() oShow!: EventEmitter<void>;

	/** Emitted when the alert closes. */
	@Event() oHide!: EventEmitter<void>;

	@Watch('open')
	handleOpenChange() {
		if (this.open) {
			this.oShow.emit();
		} else {
			this.oHide.emit();
		}
	}

	/** Shows the alert. */
	@Method()
	async show() {
		this.open = true;
	}

	/** Hides the alert. */
	@Method()
	async hide() {
		this.open = false;
	}

	private handleCloseClick = () => {
		this.hide();
	};

	private getIconDetails() {
		switch (this.variant) {
			case 'success':
				return { iconName: 'circle-check' as IconName, iconColor: 'spring_green' };
			case 'warning':
				return { iconName: 'circle-exclamation' as IconName, iconColor: 'expire_orange' };
			case 'danger':
				return { iconName: 'circle-xmark' as IconName, iconColor: 'flame_red' };
			case 'neutral':
				return { iconName: 'circle-info' as IconName, iconColor: 'main_text' };
			case 'primary':
			default:
				return { iconName: 'circle-info' as IconName, iconColor: 'accent_blue_1' };
		}
	}

	private getVariantClasses() {
		switch (this.variant) {
			case 'success':
				return 'border-border-status-success';
			case 'neutral':
				return 'border-border-status-neutral';
			case 'warning':
				return 'border-border-status-warning';
			case 'danger':
				return 'border-border-status-error';
			default:
				return 'border-border-status-info';
		}
	}

	render() {
		const { iconName, iconColor } = this.getIconDetails();

		return (
			<Host>
				<div
					class={`${
						this.open ? 'block' : 'hidden'
					} ${this.getVariantClasses()} p-4 rounded-radius-md flex items-center justify-between bg-background-default text-content-default border-t-width-xl`}
					role="alert"
					aria-hidden={!this.open}
				>
					<span class="mr-2">
						<o-icon name={iconName} color={iconColor} size="md" class="pr-7"></o-icon>
					</span>
					<span class="flex-1">
						<slot />
					</span>
					<o-icon-button name="xmark" onClick={this.handleCloseClick} aria-label="Close alert"></o-icon-button>
				</div>
			</Host>
		);
	}
}
